* {
  font-family: 'Source Sans Pro', Arial, sans-serif; }

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  background-color: white;
  width: 100vw;
  height: 65px;
  z-index: 10;
  padding: 0 20px;
  max-width: 1500px;
  margin: 0 auto; }
  nav .hamburger {
    display: none; }
  nav .menu {
    position: absolute;
    width: 100vw;
    max-width: 1500px;
    display: flex;
    justify-content: center; }
    nav .menu li {
      color: #4a4a49;
      font-size: 16px;
      font-weight: 600;
      width: auto;
      position: relative;
      padding: 0;
      margin: 20px; }
  nav .about li {
    font-size: 12px;
    color: #4a4a49; }

@media screen and (max-width: 900px) {
  nav .menu, nav .about {
    display: none; }
  nav .hamburger {
    display: block; } }

main {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw; }
  main video {
    width: 100vw;
    max-width: calc(1500px -40px); }
  main .video-wrapper {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    main .video-wrapper h1 {
      font-size: 4em;
      color: white;
      text-shadow: 1px 1px 1px #b2b2b2;
      font-weight: 600;
      margin-bottom: 20px;
      text-align: center; }
    main .video-wrapper .more {
      background-color: #e66969;
      padding: 10px 50px;
      color: white;
      border-radius: 3px;
      border: none;
      cursor: pointer;
      margin-top: 0;
      font-size: 16px;
      font-weight: 300; }
    main .video-wrapper .more:hover {
      transform: scale(1.05); }

@media screen and (max-width: 900px) {
  main video {
    height: 100vh;
    object-fit: cover; }
  main .video-wrapper h1 {
    font-size: 2.5em;
    line-height: 1em;
    margin-bottom: 20px; }
  main .video-wrapper .more {
    background-color: #e66969;
    padding: 10px 30px;
    color: white;
    border-radius: 3px;
    border: none;
    cursor: pointer;
    margin-top: 0;
    font-size: 16px;
    font-weight: 300; }
  main .video-wrapper .more:hover {
    transform: scale(1.05); } }

.adverts {
  z-index: 1; }
  .adverts .flip-card {
    cursor: zoom-in;
    background-color: transparent;
    width: 450px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1); }
  .adverts .test {
    height: 100%;
    width: 100%;
    transition: all 0.8s; }
  .adverts *:focus {
    outline: none; }
  .adverts .rotate {
    transform: rotateY(180deg); }
  .adverts .bibliotek {
    display: flex;
    flex-direction: column; }
    .adverts .bibliotek img {
      width: 100px;
      margin: 20px 50px;
      cursor: pointer;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1); }
  .adverts .remove {
    opacity: 0.2; }
  .adverts .show {
    display: block; }

.video {
  background-color: #faf0d7; }
  .video .instagram {
    width: 650px;
    z-index: 2; }
  .video video {
    position: absolute;
    width: 550px; }

@media screen and (max-width: 900px) {
  .video {
    background-color: #faf0d7; }
    .video .instagram {
      width: 450px;
      z-index: 2; }
    .video video {
      position: absolute;
      width: 300px; } }

.explanation {
  height: 100vh;
  width: 100vw;
  max-width: 1500px;
  justify-content: space-around; }
  .explanation .explanation-wrapper:hover {
    transform: scale(1.02); }
  .explanation .explanation-wrapper {
    width: 30%;
    background-color: white;
    border-radius: 3px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1); }
    .explanation .explanation-wrapper img {
      max-width: 100%;
      height: 250px;
      object-fit: cover; }
    .explanation .explanation-wrapper h2 {
      padding: 20px;
      color: #4a4a49;
      font-weight: 600;
      position: relative; }
    .explanation .explanation-wrapper p {
      text-align: left;
      padding: 0px 20px 20px 20px;
      color: #4a4a49;
      font-weight: 300; }

@media screen and (max-width: 900px) {
  .explanation {
    height: auto;
    width: 100vw;
    flex-direction: column; }
    .explanation .explanation-wrapper:hover {
      transform: scale(1.02); }
    .explanation .explanation-wrapper {
      width: 350px;
      background-color: white;
      border-radius: 3px;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
      margin: 20px 0; }
      .explanation .explanation-wrapper img {
        max-width: 350px;
        height: 250px;
        object-fit: cover; }
      .explanation .explanation-wrapper h2 {
        padding: 20px;
        color: #4a4a49;
        font-weight: 600; }
      .explanation .explanation-wrapper p {
        text-align: left;
        padding: 0px 20px 20px 20px;
        color: #4a4a49;
        font-weight: 300; } }

